<template>
    <div class='Admin'>
        <el-menu router :default-active="$route.path" class="el-menu-demo" mode="horizontal" @select="handleSelect">
            <el-menu-item index='/admin/blog'>
                <template slot='title'>
                    <i class="el-icon-files"></i>
                    <span slot="title">我的博文</span>
                </template>
            </el-menu-item>
            <el-menu-item index='/admin/draft'>
                <template slot='title'>
                    <i class="el-icon-edit-outline"></i>
                    <span slot="title">我的草稿</span>
                </template>
            </el-menu-item>
            <el-menu-item index='/admin/comment'>
                <template slot='title'>
                    <i class="el-icon-bell"></i>
                    <span slot="title">消息中心</span>
                </template>
            </el-menu-item>
            <el-menu-item index='/admin/userzone'>
                <template slot='title'>
                    <i class="el-icon-user"></i>
                    <span slot="title">个人信息管理</span>
                </template>
            </el-menu-item>

            <el-menu-item index='/admin/adminoperation' :disabled='!this.grade'>
                <template slot='title'>
                    <i class="el-icon-setting"></i>
                    <span slot="title">高级管理</span>
                </template>
            </el-menu-item>

            <el-row v-if='$route.path==="/admin/adminoperation/userOperation"||$route.path==="/admin/adminoperation/blogOperation"'>
                <el-button class='user' type='info' @click='handleClick1' icon='el-icon-s-custom'>用户管理</el-button>
                <el-button class='blog' type='info' @click='handleClick2' icon='el-icon-s-management'>博客管理</el-button>
            </el-row>

            <div class='function' >
                    <el-button type="primary" icon='el-icon-edit' @click='handleWrite' round>&nbsp发布博文</el-button>
            </div>
        </el-menu>
        <transition-box :name='name'>
            <router-view class='adminpanel'></router-view>
        </transition-box>

    </div>
</template>

<script>

    export default {
        name: "Admin",


        data() {
            return {
                name: '',
                grade: 0,
                activeIndex: '1',
                activeIndex2: '1'
            };
        },
        mounted(){
            // this.grade = sessionStorage.getItem('grade')
            // console.log(this.grade);
            this.grade = Number(sessionStorage.getItem('grade'))

        },
        watch: {
            $route(to,from){
                if (from.path==='/'){
                    return
                }
                if (from.meta.index<to.meta.index){
                    this.name='left'
                }
                else{
                    this.name='right'
                }
            }
        },
        methods: {
            handleWrite(){
                this.$router.replace('/admin/newBlog')

            },
            handleSelect(key, keyPath) {
                console.log(key, keyPath);
            },

            handleClick1(){
                this.$router.push('/admin/adminoperation/userOperation')
            },
            handleClick2(){
                this.$router.push('/admin/adminoperation/blogOperation')
            }

        }

    }
</script>

<style lang=scss scoped>
    .function{
        margin-top: 10px;
        margin-right: 20px;
        text-align: right;
        /*z-index: 1;*/
        /*position: absolute;*/
        /*top: 55px;*/
        /*right: 10px;*/
    }
    .Admin{
        position: relative;
        min-height: 630px;
    }
    .el-menu{
        z-index: 2;
        width: 100%;
        position: absolute;
        /*top: 0px;*/
        border-radius: 10px;
    }

    .adminpanel{
        position: absolute;
        top: 30px;
        left: 0px;
    }

    .el-row{
        /*margin:50px 400px;*/
        position: absolute;
        right: 170px;
        top: 18%;
    }
    .el-button{
        border-radius: 20px;
    }
    .user{
        margin-right: 15px;
    }
    .blog{

    }
</style>